

























































import { Component, Prop, Vue, Emit, Watch } from 'vue-property-decorator';
import { arrayFind } from '@utils/util';
import { dataViewFields, dataViewQuery } from '@api/viewData';
import decisionRuleEditor from '@views/processConfig/AddConfig/decisionRuleEditor.vue';

@Component({
  components: { decisionRuleEditor }
})
export default class DataAuthFilter extends Vue {
  @Prop({ default: {} }) dataAuths: any;

  @Prop({ default: false }) writable: boolean;
  @Prop({ default: [] }) dataContent: any;

  private type = 0;
  private dataViewId: number | string = '';
  private primaryKeys = '';
  private rule: Array<any> = [];
  private selections: Array<any> = [];
  private primaryCode = '';

  private innerUpdateFlag = false;

  private loading = false;

  private dataViewList: Array<any> = [];
  private dataColumns: Array<any> = [];
  private dataContents: Array<any> = [];
  created() {
    this.getViewDataOptions();
    if (this.dataContent.length < 1) {
      this.dataContents = [];
    }
  }

  @Watch('dataAuths')
  watchDataUpate(newV, oldV) {
    if ((newV && newV.dataViewId) || (oldV && oldV.dataViewId)) {
      this.dataViewId = newV.dataViewId;
      this.type = newV.type;
      this.primaryKeys = newV.primaryKeys;
      this.rule = newV.rule;
      if (newV.dataViewId || oldV.dataViewId) {
        this.getViewDataOptions();
        if (newV.dataViewId) {
          setTimeout(() => {
            this.queryChange(newV.dataViewId);
          }, 500);
        }
      }
    } else {
      this.dataColumns = [];
      this.dataContents = [];
      this.dataViewId = '';
    }
  }

  getViewDataOptions() {
    dataViewFields({ dictFlag: '0' }).then(({ data, error }) => {
      if (!error) {
        this.dataViewList = data;
      }
    });
  }

  queryChange(viewId) {
    if (this.dataViewList && this.dataViewList.length > 0) {
      const fields = arrayFind(this.dataViewList, 'id', viewId).fields;
      this.dataContents = [];
      this.dataColumns = fields.map((item) => {
        if (item.isPrimaryKey === 0) {
          this.primaryCode = item.fieldCode;
        }
        return {
          prop: item.fieldCode,
          label: item.fieldName,
          'header-align': 'left',
          showOverflowTooltip: true,
          'min-width': '120',
          fieldType: item.fieldType
        };
      });
      if (this.type === 0) {
        this.dataColumns.unshift({
          type: 'selection',
          width: 40,
          fixed: true
        });
        if (!this.writable) {
          this.dataColumns[0].selectable = () => false;
        }
      }

      this.loading = true;
      dataViewQuery({ viewId }).then(({ error, data }) => {
        this.loading = false;
        if (!error) {
          this.dataContents = data.rows;
          if (this.type === 0) {
            this.initSelection();
          }
        } else {
          this.$message.error(error.message);
        }
      });
    }
  }

  handleDataSelection(selection) {
    if (!this.innerUpdateFlag) {
      this.selections = selection;
    }
  }

  initSelection() {
    if (this.primaryKeys) {
      this.$nextTick(() => {
        this.innerUpdateFlag = true;
        const authDatas: any = this.$refs.multipleTable;
        this.primaryKeys.split(',').forEach((key) => {
          const record = arrayFind(this.dataContents, this.primaryCode, key);
          authDatas.toggleRowSelection(record, true);
          this.selections.push(record);
        });
        this.innerUpdateFlag = false;
      });
    }
  }

  getCurrentDataAuths() {
    return {
      dataViewId: this.dataViewId,
      rule: this.rule,
      primaryKeys: this.selections
        .filter((item) => item)
        .map((item) => item[this.primaryCode])
        .join(','),
      type: this.type
    };
  }
  clearEmpty() {
    this.dataContents = [];
  }
}
