<template>
  <base-panel :title="title" class="group-detail base-panel">
    <el-row :gutter="30">
      <el-col :span="12">
        <div class="group-info">
          <span class="gangweiFont">岗位名称：</span>
          <el-input
            v-model="station.name"
            class="group-info-input"
            placeholder="请填写岗位名称"
            @input="handleStationNameChange"
          ></el-input>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="group-info">
          <span class="gangweiFont">岗位性质：</span>
          <el-select
            v-model="station.groupNature"
            clearable
            filterable
            placeholder="请选择岗位性质"
            class="group-info-input"
          >
            <el-option
              v-for="item in natureList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="30">
      <el-col :span="12">
        <div class="group-info">
          <span class="gangweiFont">上级岗位：</span>
          <el-select
            v-model="station.parentId"
            clearable
            filterable
            placeholder="请选择上级岗位"
            class="group-info-input"
            @change="queryChange"
          >
            <el-option
              v-for="item in simplePostList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="group-info">
          <span class="gangweiFont">岗位描述：</span>
          <el-input
            v-model="station.description"
            class="group-info-input"
            placeholder="请填写岗位描述"
          ></el-input>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="group-info group-info-type">
          <span class="gangweiFont">数据权限类型：</span>
          <el-radio-group
            v-model="station.dataAuthorityType"
            style="display:inline-block;width:250px;"
            class="group-info-input"
            @change="changeAuth"
          >
            <el-radio :label="0">公共授权</el-radio>
            <el-radio :label="1">用户独立授权</el-radio>
          </el-radio-group>
        </div>
      </el-col>
      <el-col v-if="station.dataAuthorityType === 0" :span="4">
        <div class="group-info group-info-btn">
          <el-button size="mini" icon="el-icon-plus" @click="assetOnly">
            数据授权
          </el-button>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="30">
      <!-- 拥有的用户 -->
      <el-col :span="12">
        <note-card
          class="addUser"
          :title="'已拥有用户' + '(' + station.users.length + ')'"
          :actions="userTitleBtns()"
        >
          <base-table
            height="300"
            :data-source="station.users"
            :columns="userColumns()"
            :show-summary="false"
            @selectionChange="handleOwnsUserSelected"
          ></base-table>
        </note-card>
      </el-col>
      <!-- 拥有的角色 -->
      <el-col :span="12">
        <note-card
          class="addUser"
          :title="'已拥有角色' + '(' + station.roles.length + ')'"
          :actions="roleTitleBtns()"
        >
          <base-table
            height="300"
            :data-source="station.roles"
            :columns="roleColumns()"
            :show-summary="false"
            @selectionChange="handleOwnsRoleSelected"
          ></base-table>
        </note-card>
      </el-col>
    </el-row>
    <!--新增/修改-->
    <simple-dialog
      dialog-title="数据筛选"
      :dialog-visible.sync="dataVisible"
      dialog-width="66%"
      save-text="确 定"
      cancel-text="取 消"
      botton-show="true"
      @saveEmit="certain"
      @cancelEmit="cancelDataSelect"
    >
      <dataAuthFilter
        :dataAuths="dataAuths"
        :dataContent="dataContents"
        ref="datasFilter"
        :writable="true"
        style="height: 460px;"
      ></dataAuthFilter>
    </simple-dialog>
    <!--添加用户-->
    <simple-dialog
      class="addUser"
      :dialog-title="addTitle"
      :dialog-visible.sync="userVisible"
      dialog-width="477px"
      save-text="确 定"
      cancel-text="取 消"
      botton-show="true"
      @saveEmit="certainAddUser"
      @cancelEmit="cancelAddUser"
    >
      <div>
        <div>
          <el-form
            @submit.native.prevent
            :inline="true"
            size="mini"
            class="operate-left-form"
          >
            <el-form-item v-if="addTitle === '用户筛选'">
              <el-input
                v-model="userQueryStr"
                placeholder="请输入要搜索的内容"
                @input="inputQuery"
              >
                <div slot="suffix" ref="inputColor1" class="backgroundChange">
                  <icon class="icon" name="icon_search" />
                </div>
              </el-input>
            </el-form-item>
            <el-form-item v-else>
              <el-input
                v-model="roleQueryStr"
                placeholder="请输入要搜索的内容"
                @input="inputQuery"
              >
                <div slot="suffix" ref="inputColor1" class="backgroundChange">
                  <icon class="icon" name="icon_search" />
                </div>
              </el-input>
            </el-form-item>
            <el-form-item v-if="addTitle !== '角色筛选'">
              <el-select
                v-model="deptId"
                clearable
                @change="refreshUserList"
                placeholder="请选择所属部门"
              >
                <el-option
                  v-for="item in departments"
                  :key="item.id"
                  :label="item.deptName"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </div>
        <base-table
          ref="multipleTable"
          height="300"
          :data-source="addUserOrRole"
          :loading="loading"
          :columns="filterColumns()"
          :show-summary="false"
          @selectionChange="addUsersRoles"
        ></base-table>
      </div>
    </simple-dialog>
    <roleDialog
      :roleOperType="roleOperType"
      ref="child"
      @complated="handleRoleCreated"
    />
    <div class="bottomBtn">
      <el-button
        class="btn"
        type="primary"
        @click="handleSave"
        :loading="confirmLoading"
      >
        确 定
      </el-button>
      <el-button class="btn" @click="handleCancel">取 消</el-button>
    </div>
  </base-panel>
</template>
<script>
import { mapState, mapMutations } from 'vuex';
import { cloneDeep, differenceWith } from 'lodash';
import mixin from '../mixin';
import vue from 'vue';
import { arrayFind, uniqueArray } from '@utils/commonUtil';
import roleDialog from '../roleManage/dialog';
import dataAuthFilter from './dataAuthFilter';
import {
  userGroupRoleQuery,
  getUsersByGroup,
  roleAuthQuery,
  userGroupAdd,
  userGroupEdit,
  userGroupAuthorize,
  dataAuthAdd,
  postAdd,
  postListDic,
  postEdit,
  postDel,
  postDetail,
  dataAuthEdit,
  dataAuthDel,
  dataAuthDetail
} from '../../../api/authority.js';
import { dataViewQuery } from '../../../api/viewData';
export default {
  name: 'UserGroupDetail',
  components: {
    roleDialog,
    dataAuthFilter
  },
  mixins: [mixin],
  data() {
    const _this = this;
    return {
      confirmLoading: false,
      title: '岗位管理-新增',
      selectChange: false,
      operateType: '0',
      detailId: null,
      leftUsers: [],
      users: [],
      userLock: false,
      roles: [],
      roleLock: false,
      userLimitNumber: 16,
      userLoadBtn: true,
      roleLoadBtn: true,
      roleLimitNumber: 10,
      addUserOrRole: [],
      addTitle: '用户筛选',
      station: {
        dataAuthorityType: 0,
        name: '',
        parentId: '',
        description: '',
        dataGroupId: '',
        users: [],
        roles: [],
        groupNature: '001'
      },
      dataAuths: {
        dataViewId: '',
        rule: [],
        primaryKeys: '',
        type: 0
      },
      authingUser: null,
      userTitleBtns() {
        const _this = this;
        return [
          {
            title: '添加用户',
            icon: 'add-line',
            onClick: _this.addUser
          },
          {
            title: '批量删除',
            icon: 'icon_delete',
            onClick: _this.delUser
          }
        ];
      },
      roleTitleBtns() {
        const _this = this;
        return [
          {
            title: '添加角色',
            icon: 'add-line',
            onClick: _this.addRole
          },
          {
            title: '批量删除',
            icon: 'icon_delete',
            onClick: _this.delRole
          }
        ];
      },
      userColumns() {
        const _this = this;
        return [
          {
            type: 'selection',
            'min-width': '50',
            align: 'center'
          },
          {
            prop: 'username',
            label: '姓名',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '120'
          },
          {
            prop: 'deptName',
            label: '部门',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '120'
          },
          {
            prop: '',
            label: '操作',
            align: 'center',
            'min-width': '180',
            actions: [
              {
                code: 'dataAuth',
                text: '数据授权',
                hidden: _this.hiddenAuthBtn,
                onClick: _this.assetAuth
              },
              {
                code: 'dataAuth',
                text: '删除授权',
                hidden: _this.hiddenDelAuthBtn,
                onClick: _this.assetAuthDel
              },
              {
                code: 'del',
                text: '删除',
                onClick: _this.assetDel
              }
            ]
          }
        ];
      },
      filterColumns() {
        if (this.addTitle === '用户筛选') {
          return [
            {
              type: 'selection',
              'min-width': '50',
              align: 'center'
            },
            {
              prop: 'username',
              label: '姓名',
              'header-align': 'left',
              showOverflowTooltip: true,
              'min-width': '120'
            },
            {
              prop: 'deptName',
              label: '部门',
              'header-align': 'left',
              showOverflowTooltip: true,
              'min-width': '120'
            }
          ];
        } else {
          return [
            {
              type: 'selection',
              'min-width': '50',
              align: 'center'
            },
            {
              prop: 'name',
              label: '角色名称',
              'header-align': 'left',
              showOverflowTooltip: true,
              'min-width': '100'
            },
            {
              prop: 'description',
              label: '角色描述',
              'header-align': 'left',
              showOverflowTooltip: true,
              'min-width': '200'
            }
          ];
        }
      },
      roleColumns() {
        const _this = this;
        return [
          {
            type: 'selection',
            'min-width': '50',
            align: 'center'
          },
          {
            prop: 'name',
            label: '角色名称',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '200'
          },
          {
            prop: '',
            label: '操作',
            align: 'center',
            'min-width': '180',
            actions: [
              {
                code: 'del',
                text: '删除',
                hidden: (record) => record.flag,
                onClick: _this.actionDel
              },
              {
                code: 'show',
                text: '查看详情',
                hidden: (record) => record.flag,
                onClick: _this.showRoleDetail
              },
              {
                code: 'shouquan',
                text: '授权',
                hidden: (record) => !record.flag,
                onClick: _this.actionAuth
              }
            ]
          }
        ];
      },
      dataVisible: false,
      userVisible: false,
      selectResult: [],
      actionFlag: {},
      selectType: '',
      simplePostList: [],
      selectionUser: [],
      selectionRole: [],
      selectRows: [], // 默认勾选的
      keepUser: [], // 临时添加的用户
      keepRole: [], // 临时添加的角色
      roleOperType: false,
      preperDelDatas: [],
      dataContents: [],
      natureList: [
        { id: '001', name: '员工岗' },
        { id: '002', name: '业务组长岗' },
        { id: '003', name: '业务主管岗' },
        { id: '004', name: '部门行政负责人' },
        { id: '005', name: '系统管理员' }
      ]
    };
  },
  watch: {
    dataVisible(newVal, oldVal) {
      if (newVal === false) {
        this.$refs.datasFilter.clearEmpty();
      }
    }
  },
  created() {
    this.operateType = this.$route.query.type;
    this.rolePageSize = 10000;
    this.userPageSize = 10000;
    this.initDeptments();
    this.simplePostQuery();

    if (this.operateType === '0') {
      //  新增
      this.title = '岗位管理-新增';
    } else {
      this.detailId = this.$route.query.id;
      this.title = '岗位管理-修改';
      postDetail(this.detailId).then(({ data, error }) => {
        if (!error) {
          const station = {
            id: data.id,
            name: data.name,
            dataAuthorityType: 0,
            parentId: '',
            description: '',
            dataGroupId: '',
            users: [],
            roles: []
          };
          if (data.parentId) {
            station.parentId = data.parentId;
          }
          if (data.dataAuthorityType) {
            station.dataAuthorityType = data.dataAuthorityType;
          }
          if (data.description) {
            station.description = data.description;
          }
          station.groupNature = data.groupNature ? data.groupNature : '';
          if (data.dataGroupId) {
            station.dataGroupId = data.dataGroupId;
          }
          if (data.users) {
            station.users = data.users;
          }
          if (data.roles && data.roles.length > 0) {
            const exist = arrayFind(data.roles, 'name', data.name);
            if (!exist) {
              data.roles.unshift({ name: data.name });
            }
            station.roles = data.roles;
          } else {
            station.roles.push({ name: data.name });
          }
          this.station = station;
          if (this.station.roles.length === 0) {
            this.addDefautRole(this.station.name);
          }
        } else {
          this.$message.error(error.message);
        }
      });
    }
  },
  methods: {
    // 用户筛选/角色筛选 搜索功能
    inputQuery() {
      if (this.addTitle === '角色筛选') {
        this.refreshRoleList();
      } else {
        this.refreshUserList();
      }
    },
    // 简单岗位列表查询
    simplePostQuery() {
      postListDic().then(({ data, error }) => {
        if (!error) {
          this.simplePostList = data;
        } else {
          this.$message.error(error.message);
        }
      });
    },
    refreshRoleList() {
      this.getRoleList().then(() => {
        this.filterSelected();
      });
    },
    // 数据筛选弹窗
    assetOnly() {
      this.assetAuth();
    },
    // 数据授权
    assetAuth(row) {
      this.dataVisible = true;
      let id = null;
      if (row) {
        this.authingUser = row;
        if (row.dataGroupId) {
          id = row.dataGroupId;
        }
      } else if (this.station.dataGroupId) {
        id = this.station.dataGroupId;
        this.authingUser = null;
      }
      if (id) {
        dataAuthDetail(id).then(({ data, error }) => {
          if (!error) {
            this.dataAuths = data;
          }
        });
      }
    },
    // 提交数据授权
    certain() {
      const dataAuths = this.$refs.datasFilter.getCurrentDataAuths();
      if (dataAuths) {
        let fun = dataAuthAdd;
        // 用户独立授权
        if (this.station.dataAuthorityType === 1) {
          if (this.authingUser.dataGroupId) {
            fun = dataAuthEdit;
            dataAuths.id = this.authingUser.dataGroupId;
          }
          // 用户公共授权
        } else {
          if (window.sessionStorage.getItem('dataGroupId')) {
            fun = dataAuthEdit;
            dataAuths.id = window.sessionStorage.getItem('dataGroupId');
          }
        }
        fun(dataAuths).then(({ data, error }) => {
          if (!error) {
            this.$message.success({
              title: '成功',
              message: '数据授权成功!'
            });
            this.changeAuth();
            if (fun === dataAuthAdd) {
              if (this.station.dataAuthorityType === 0) {
                window.sessionStorage.setItem('dataGroupId', data);
                this.station.dataGroupId = window.sessionStorage.getItem(
                  'dataGroupId'
                );
              } else {
                this.authingUser.dataGroupId = data;
              }
            }
          } else {
            this.$message.error({
              title: '失败',
              message: `数据授权失败: ${error.message}`
            });
          }
        });
      }
    },
    changeAuth() {
      this.dataAuths = {
        dataViewId: '',
        rule: [],
        primaryKeys: '',
        type: 0
      };
      this.dataContents = [];
      this.dataVisible = false;
    },
    cancelDataSelect() {
      this.changeAuth();
    },
    // 删除用户
    assetDel(row) {
      this.$confirm(
        '确定要删除该用户吗，用户拥有的数据权限也会被删除！',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }
      ).then(() => {
        if (row.dataGroupId) {
          this.preperDelDatas.push(row.dataGroupId);
        }
        this.station.users = this.station.users.filter(
          (item) => item.id !== row.id
        );
      });
    },
    // 删除角色
    actionDel(row) {
      this.$confirm('确定要删除该角色吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.station.roles = this.station.roles.filter(
          (item) => item.id !== row.id
        );
      });
    },
    assetAuthDel(row) {
      this.$confirm('确定要删除该数据权限吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.doDataAuthDel(row.dataGroupId, true);
      });
    },
    doDataAuthDel(id, message) {
      dataAuthDel({ id }).then(({ error }) => {
        if (!error) {
          this.$message.success('删除数据权限成功!');
          // 同时隐藏删除授权按钮：设置dataGroupId=null
          this.station.users.forEach((item, idx) => {
            if (item.dataGroupId == id) {
              this.station.users[idx].dataGroupId = null;
              return;
            }
          });
        } else {
          if (message) {
            this.$message.error(error.message);
          }
        }
      });
    },

    // 添加用户
    addUser() {
      this.userVisible = true;
      this.addTitle = '用户筛选';
      if (this.userList.length === 0) {
        this.getUsers().then(() => {
          this.filterSelected();
        });
      } else {
        this.filterSelected();
      }
    },
    // 添加角色
    addRole() {
      this.userVisible = true;
      this.addTitle = '角色筛选';
      if (this.roleList.length === 0) {
        this.getRoleList().then(() => {
          this.filterSelected();
        });
      } else {
        this.filterSelected();
      }
    },
    filterSelected() {
      let soucce = this.roleList;
      let owns = this.station.roles;
      if (this.addTitle === '用户筛选') {
        soucce = this.userList;
        owns = this.station.users;
      }
      this.addUserOrRole = differenceWith(soucce, owns, function(s, o) {
        return s.id === o.id;
      });
    },
    handleOwnsUserSelected(selection) {
      this.selectionUser = selection;
    },
    handleOwnsRoleSelected(selection) {
      this.selectionRole = selection;
    },
    // 批量删除用户
    delUser() {
      if (this.selectionUser.length > 0) {
        this.$confirm(
          '确定要删除选中的用户吗，用户拥有的数据权限也会被删除！',
          '提示',
          {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            center: true
          }
        ).then(() => {
          this.station.users = differenceWith(
            this.station.users,
            this.selectionUser,
            function(s, o) {
              if (s.id === o.id) {
                if (o.dataGroupId) {
                  this.preperDelDatas.push(o.dataGroupId);
                }
                return true;
              } else {
                return false;
              }
            }
          );
        });
      } else {
        if (this.station.users.length > 0) {
          this.$message.error('请先勾选用户;');
        } else {
          this.$message.error('请先添加用户;');
        }
      }
    },
    // 批量删除角色
    delRole() {
      if (this.selectionRole.length > 0) {
        // 判断是否包含默认角色
        let selectDefault = false;
        this.selectionRole.forEach((item, index) => {
          if (item.name === this.station.name) {
            selectDefault = true;
            return;
          }
        });
        if (selectDefault) {
          this.$message.error('不能删除默认岗位角色');
          return false;
        }
        this.$confirm('确定要删除选中的角色吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.station.roles = differenceWith(
            this.station.roles,
            this.selectionRole,
            function(s, o) {
              return s.id === o.id;
            }
          );
        });
      } else {
        if (this.station.roles.length > 0) {
          this.$message.error('请先勾选角色;');
        } else {
          this.$message.error('请先添加角色;');
        }
      }
    },
    // 新增岗位勾选用户或角色
    addUsersRoles(row) {
      if (this.addTitle === '角色筛选') {
        this.keepRole = row;
      } else {
        this.keepUser = row;
      }
    },
    // 用户筛选或角色筛选保存按钮
    certainAddUser() {
      if (this.addTitle === '角色筛选') {
        this.station.roles.push(...this.keepRole);
      } else {
        this.station.users.push(...this.keepUser);
      }
      this.cancelAddUser();
    },
    cancelAddUser() {
      this.userVisible = false;
      this.$refs.multipleTable.clearSelection();
      this.keepRole = [];
      this.keepUser = [];
    },
    // 功能授权
    actionAuth(row) {
      this.roleOperType = '3';
      this.$refs.child.showAddInfo(false, row, this.roleOperType);
    },
    handleStationNameChange(val) {
      if (this.station.roles.length === 0) {
        this.addDefautRole(val);
      } else {
        // 如果先新增角色然后修改岗位名称 做了个判断
        const flag = this.station.roles[0].flag;
        if (flag) {
          this.station.roles[0].name = val;
        } else {
          const obj = {
            name: val,
            flag: 'group'
          };
          this.station.roles.unshift(obj);
        }
      }
    },
    addDefautRole(val) {
      const obj = {
        name: val,
        flag: 'group'
      };
      if (val !== '') {
        if (this.station.roles.length > 0) {
          if (this.station.roles[0].flag) {
            this.station.roles[0].name = val;
          } else {
            this.station.roles.splice(0, 0, obj);
          }
        } else {
          this.station.roles.push(obj);
        }
      } else {
        if (this.station.roles.length > 0) {
          if (this.station.roles[0].flag) {
            this.station.roles.splice(0, 1);
          }
        }
      }
    },
    handleRoleCreated(id) {
      if (id) {
        this.station.roles[0].id = id;
      }
    },
    showRoleDetail(record) {
      this.roleOperType = '2';
      this.$refs.child.showAddInfo(true, record, this.roleOperType);
    },
    refreshUserList() {
      this.getUsers().then(() => {
        this.filterSelected();
      });
    },
    hiddenAuthBtn() {
      return this.station.dataAuthorityType === 0;
    },
    hiddenDelAuthBtn(record) {
      return this.station.dataAuthorityType === 0 || !record.dataGroupId;
    },
    handleSave() {
      // 岗位新增
      const param = {
        name: this.station.name,
        parentId: this.station.parentId,
        dataAuthorityType: this.station.dataAuthorityType,
        description: this.station.description,
        dataGroupId: window.sessionStorage.getItem('dataGroupId'),
        users: this.station.users,
        groupNature: this.station.groupNature
        // users: this.station.users.map((item) => {
        //   const user = { id: item.id };
        //   if (this.station.dataAuthorityType === 1 && item.dataGroupId) {
        //     user.dataGroupId = item.dataGroupId;
        //   }
        //   return user;
        // })
      };
      const tempRoles = cloneDeep(this.station.roles);
      if (!tempRoles[0].id) {
        tempRoles.shift();
      }
      param.roles = tempRoles.map((item) => ({ id: item.id }));

      if (param.name === '') {
        this.$message.error('请填写岗位名称!');
      } else if (param.dataAuthorityType === '') {
        this.$message.error('请选择数据权限类型!');
      } else if (!param.groupNature) {
        this.$message.error('请选择岗位性质!');
      } else {
        let fun = postAdd;
        let msg = '新增';
        if (this.operateType === '1') {
          fun = postEdit;
          msg = '修改';
          param.id = this.detailId;
        }
        // 公共授权
        if (param.dataAuthorityType === 0) {
          this.station.users.forEach((ele) => {
            delete ele.dataGroupId;
          });
        } else {
          // 用户独立授权
          param.dataGroupId = '';
        }
        this.confirmLoading = true;
        fun(param).then(({ error }) => {
          if (!error) {
            this.$store.dispatch('freshManage'); // 用于岗位管理页面刷新
            this.$message.success(`${msg}岗位成功!`);
            this.confirmLoading = false;
            this.$router.push({ name: 'postManagement' });
            // 删除废弃的数据权限组
            this.preperDelDatas.forEach((id) => this.doDataAuthDel(id));
            window.sessionStorage.removeItem('dataGroupId');
          } else {
            this.confirmLoading = false;
            this.$message.error(`${msg}岗位失败: ${error.message}`);
          }
        });
      }
    },
    handleCancel() {
      this.$router.push({ name: 'postManagement' });
    }
  }
};
</script>
